import React from 'react';
import timespan from '../utils/timespan';
import publicUrl from '../utils/publicURL';
import css from './Post.module.css';

function Post(props){
    const comments = props.comments
    const commentItems = comments.map((comment)=>
        <div>{comment.userId} {comment.text}</div>
    );

    return(
        <div className='post'>
            <div className='postItem'>
                <img src = {props.user.photo} alt='userPhoto' className='userPhoto'></img>
                {props.user.id}
            </div>
            <div className='postItem'>
                <img src = {props.post.photo} alt='post' className='postPhoto'></img>
            </div>
            <div>
                <img src = {publicUrl('/assets/like.svg')} alt = 'like'></img>
                <img src = {publicUrl('/assets/comment.svg')} alt = 'comment'></img>
            </div>
            <div className='postItem'>
                {props.likes.count} likes
            </div>
            <div className='postItem'>
                {props.post.userId} {props.post.desc}
                {commentItems}
            </div>
            <div className = 'postItem'>
                {timespan(props.post.datetime)} ago
            </div>
        </div>
    )
};

export default Post;